// 导航栏组件
import cuCustom from '@/colorui/components/cu-custom.vue';
// 
import Modal from '@/components/x-modal/x-modal'
export default {
	components: {
		cuCustom,
		Modal
	},
	data() {
		return {
			// 控制自定义导航栏是否字体加粗
			isBold: true,
			// 用户id
			userid:1,
			// 订单列表
			orderlist:[],
			show: false,
			title:'',
		};
	},
	methods: {
		ck(num){
			console.log(num)
		},
		gainOrder(){
			uni.request({
				url: 'http://47.104.29.236:9999/api/v1/gainOrder',
				method: 'POST',
				data:{userid:this.userid},
				success: res => {
					// console.log(res.data.data.reverse())
					
					this.orderlist = res.data.data.reverse()
				},
				fail: () => {},
				complete: () => {}
			});
		},
		// 评论
		btn(id){
			uni.navigateTo({
				url: '../evaluate/evaluate?id='+id,
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		// 查看评论
		examine(comment){
			this.show = true
			this.title = comment
			console.log(comment)
		},
		confirm(e){
			   console.log(e,"成功");
		},
		event(e){
		console.log(e)
		},
	},
	onLoad(e) {
		this.gainOrder()
		uni.getStorage({
		    key: 'user_id',
		    success: function (res) {
		        // console.log(res.data);
				this.userid = res.data
		    }
		})
	}
};
